@import "../common/styles/sass/vareables";
@import "../common/styles/sass/mixins";

.contactBlock {
  background-color: $backgroundColor;
  padding: 30px 0;

  .contactContainer {
    flex-direction: column;
    justify-content: center;

    .form {
      width: 80%;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      margin-bottom: 20px;
      margin-top: 20px;
      @media screen and (min-width: 700px) {
        width: 60%;
      }
    }

    & input, & textarea {
      width: 100%;
      margin: 5px 0;
      background-color: $backgroundColor;
      border: 1px solid $textColor;
      @include text($fontFamilyDescription, 12px, 400, 1.2, $textColor)
    }

    & textarea {
      height: 150px;
    }

  }
}




