@import "../../styles/sass/vareables";
@import "../../styles/sass/mixins";

.title {
  padding-bottom: 10px;
  position: relative;
  @include text($fontFamilyTitle, 36px, 600, 1.2, $textColor);
  & h4 {
    margin: 0px;
    font-weight: inherit;
  }

  &::after  {
    content: "";
    width: 50%;
    height: 1px;
    background: $primaryColor;
    position: absolute;
    bottom: 0;
    //left: calc(50% - 30px);
    left: 50%;
    transform: translate(-50%, 0%);
  }

  &::before{
    content: '';
    width: 100%;
    height: 1px;
    background: $descriptionColor;
    opacity: 0.3;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}


